import Vue from 'vue'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

import './assets/css/bootstrap.min.css';
import './assets/css/themify-icons.css';
import './assets/css/animate.min.css';
import './assets/css/main.css';
import './assets/css/responsive.css';

import { library } from '@fortawesome/fontawesome-svg-core';
import { faLanguage } from '@fortawesome/free-solid-svg-icons';
import { faYoutube, faMedium, faFacebook, faTelegramPlane, faTwitter, faReddit, faDiscord, faInstagram, faTiktok } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

library.add(faYoutube, faMedium, faFacebook, faLanguage, faTwitter, faTelegramPlane, faReddit,faDiscord, faInstagram, faTiktok );

Vue.component('font-awesome-icon', FontAwesomeIcon);

import VueScrollTo from 'vue-scrollto';
Vue.use(VueScrollTo);



new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
