<template>
  <header class="header">
    <!--start navbar-->
    <nav
        class="navbar navbar-expand-lg fixed-top"
        v-bind:class="{
                affix: hasAffix,
                'custom-nav': coloredLogo,
                'bg-transparent': !coloredLogo,
                'white-bg': coloredLogo,
            }"
    >
      <div class="container">
        <a class="navbar-brand" href="/"
        ><img
            :src="
                            coloredLogo
                                ? 'img/logo.png'
                                : 'img/logo.png'
                        "
            width="150"
            alt="logo"
            class="img-fluid"
        /></a>

        <button
            class="navbar-toggler"
            type="button"
            @click="mobileNavClicked"
            v-bind:class="{ collapsed: collapsed }"
        >
          <span><img src="img/pixelated_banana.png" class="img-burger"></span>
        </button>

        <div class="collapse navbar-collapse main-menu h-auto" v-bind:class="{ show: !collapsed }"
             id="navbarSupportedContent">
          <ul class="navbar-nav text-sm-center">
            <li class="nav-item">
              <a class="nav-link page-scroll" href="#meetScawy" v-scroll-to="'#meetScawy'">Meet scawy</a>
            </li>
            <li class="nav-item">
              <a class="nav-link page-scroll" href="#nft" v-scroll-to="'#nft'">NFT</a>
            </li>
            <li class="nav-item">
              <a class="nav-link page-scroll" href="#moods" v-scroll-to="'#moods'">Moods
              </a>
            </li>
            <li class="nav-item">
              <a class="nav-link page-scroll" href="#roadmap" v-scroll-to="'#roadmap'">Roadmap
              </a>
            </li>
          </ul>
          <ul class="navbar-nav ml-auto text-sm-center">
            <li class="list-inline-item">
              <a href="https://x.com/scawy_yo" target="_blank" title="X">
                <font-awesome-icon :icon="['fab', 'twitter']" size="lg" :style="{ color: 'white' }"/>
              </a>
            </li>
            <li class="list-inline-item">
              <a href="https://discord.gg/Hg4R94SnRu" target="_blank" title="Discord">
                <font-awesome-icon :icon="['fab', 'discord']" size="lg" :style="{ color: 'white' }"/>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <!--end navbar-->
  </header>
</template>

<style>
.navbar-brand {
  color: #fff;
  font-size: 48px;
  font-weight: bold;
  font-family: 'Cinzel Decorative', sans-serif;
}
</style>

<script>

export default {
  props: {
    coloredLogo: {
      type: Boolean,
      default: false,
    },
  },
  data: function () {
    return {
      windowTop: 0,
      collapsed: true,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    onScroll: function () {
      this.windowTop = window.top.scrollY;
    },
    mobileNavClicked: function () {
      this.collapsed = !this.collapsed;
    },
  },
  computed: {
    hasAffix: function () {
      return this.windowTop > 0;
    },
  },
};
</script>

