import Vue from 'vue';
import VueRouter from 'vue-router';

import Scawy from '../pages/indices/Scawy.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Scawy',
    component: Scawy,
    meta: { title: 'Scawy' }
  },
  // {
  //   path: '/whisper',
  //   name: 'Scawy',
  //   component: Whisper,
  //   meta: { title: 'Scawy' }
  // },
  // {
  //   path: '/nft',
  //   name: 'Scawy',
  //   component: NFT,
  //   meta: { title: 'Scawy' }
  // },
  {
    path: '*',
    name: 'catchAll',
    component: Scawy,
    meta: { title: 'Scawy' }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      const element = document.getElementById(to.meta.anchor);
      if (element) {
        return window.scrollTo({
          top: element.offsetTop,
          behavior: 'smooth'
        });
      }
    } else if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 }
    }
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta && to.meta.title)
    document.title = to.meta.title;
  next();
});


export default router
